import AbstractEditable from './abstract';

export default class CheckboxEditable extends AbstractEditable {

	render() {
		const group = this.getElement().querySelector( '.input__group.-checkbox' );

		Object.keys( this._args.options ).forEach( ( k ) => {
			let option = this._args.options[ k ];

			group.insertAdjacentHTML( 'beforeEnd', this._template.form.checkbox( 'option[]', option.value, option.text ).outerHTML );
		} );
	}

	valueToInput( values ) {
		if ( Array.isArray( values ) ) {
			values.forEach( ( value ) => {
				let input = this._element.querySelector( `input[value="${value}"]` );

				if ( input ) {
					input.checked = true;
				}
			} );
		}
	}

	getValue() {
		let array = [];

		this.getElement().querySelectorAll( 'form input[type=checkbox]:checked' ).forEach( c => array.push( c.value ) );

		return array;
	}

	getTemplate() {
		return `<div class="input__group -checkbox"></div>`;
	}

	_setDefaults() {
		this._defaults.options = [];
		this._defaults.type = 'checkbox';
	}
}