import Middleware from '../middleware/editable';
import IECell from './inline-edit-cell';

export default class InlineEdit {

	constructor( Table, Editables, enabled ) {
		this.Editables = Editables;
		this.Table = Table;
		this.Middleware = new Middleware( this.Editables );

		this._enabled = enabled;
		this._initialised = false;
		this._open_editables = [];

		this._setTableClass();
		this._addEvents();
		this._initCells();
	}

	isEnabled() {
		return this._enabled;
	}

	enable() {
		this._enabled = true;
		this._storePreference();
		this._setTableClass();
		this._initCells();
	}

	disable() {
		this._enabled = false;
		this._storePreference();
		this._setTableClass();
	}

	/**
	 * @param {Boolean} initialized
	 * @returns {InlineEdit}
	 */
	setInitialised( initialized = true ) {
		this._initialised = initialized;

		return this;
	}

	/**
	 * @returns {boolean}
	 */
	isInitialised() {
		return this._initialised;
	}

	_setTableClass() {
		let table = this.Table.el;

		table.classList.add( 'acp-ie-table' );

		if ( this.isEnabled() ) {
			table.classList.add( 'acp-ie-enabled' );
			table.classList.remove( 'acp-ie-disabled' );
		} else {
			table.classList.add( 'acp-ie-disabled' );
			table.classList.remove( 'acp-ie-enabled' );
		}
	}

	_initCells() {
		if ( this.isInitialised() || !this.isEnabled() ) {
			return;
		}

		let columns = this.Table.Columns.getColumns();

		Object.keys( columns ).forEach( ( column_name ) => {
			let settings = columns[ column_name ];

			if ( !settings || !settings.editable || !settings.editable.type ) {
				return;
			}

			let middleware = this.Middleware.get( settings );
			let editable = middleware.getEditable();

			if ( !editable ) {
				console.info( `${settings.editable.type} does not exist or is not loaded` );
				return;
			}

			this.Table.Cells.getByName( column_name ).forEach( ( cell ) => {
				if ( cell.dataStorage ) {
					cell.IEdit = new IECell( this, cell, middleware, editable );
				}
			} );

		} );

		this.setInitialised();
	}

	updateRowCells( e ) {
		let row = e.target;
		let id = this.Table._getIDFromRow( row );

		let columns = this.Table.Columns.getColumns();
		Object.keys( columns ).forEach( ( column_name ) => {
			let cell = this.Table.Cells.get( id, column_name );

			cell.el = this.Table.getRowCellByName( row, column_name );
			this.Table._addColumnCellMethods( cell );

			if ( cell.IEdit ) {
				cell.IEdit.afterSetValue();
			}
		} );

	}

	_storePreference() {
		let value = this.isEnabled() ? 1 : 0;

		return jQuery.post( ajaxurl, {
			action : 'acp_editing_single_request',
			method : 'editability_state',
			value : value,
			list_screen : AC.list_screen,
			layout : AC.layout,
			_ajax_nonce : AC.ajax_nonce
		} );
	}

	_addEvents() {
		let self = this;
		let input = document.getElementById( 'acp-enable-editing' );

		if ( input ) {
			input.addEventListener( 'change', () => {
				if ( input.checked ) {
					self.enable();
				} else {
					self.disable();
				}
			} );
		}

		document.addEventListener( 'ACP_InlineEditing_Close', () => {
			this.closeOpenEditables();
		} );

		this.updateRowCell = this.updateRowCells.bind( this );
		jQuery( this.Table.el ).on( 'updated', 'tr', this.updateRowCell );

	}

	closeOpenEditables() {
		this._open_editables.forEach( ( editable ) => {
			editable.close();
		} );

		this._open_editables = [];
	}

}