import {getColumnValueFromRowHTML} from "../helpers/rows";

export default class TableUpdate {

	updateSelectedCells( column, value = '' ) {
		let ids = AdminColumns.Table.Selection.getIDs();

		if ( ids.length === 0 ) {
			return;
		}

		this.getRows( ids ).done( ( response ) => {
			if ( !response.hasOwnProperty( 'data' ) || !response.data.hasOwnProperty( 'table_rows' ) ) {
				return;
			}

			let rows = response.data.table_rows;

			Object.keys( rows ).forEach( ( id ) => {
				let cell = AdminColumns.Table.Cells.get( id, column );
				let iEdit = cell.IEdit;

				if ( iEdit ) {
					iEdit.setValueFromRowHTML( rows[ id ] );
				} else {
					cell.getElement().innerHTML = getColumnValueFromRowHTML( rows[ id ], column );
				}
			} );

			this.updateSelectedEditingCells( column, rows, value );
		} );

	}

	updateSelectedEditingCells( column, rows, value ) {
		let ids = AdminColumns.Table.Selection.getIDs();

		if ( ids.length === 0 ) {
			return;
		}

		let Editing_items = jQuery.ajax( {
			url : ajaxurl,
			method : 'post',
			data : {
				action : 'acp_editing_single_request',
				method : 'get_editable_values',
				ids : ids,
				column : column,
				list_screen : AC.list_screen,
				layout : AC.layout,
				_ajax_nonce : AC.ajax_nonce
			}
		} );

		Editing_items.done( ( response ) => {
			response.data.editable_values.forEach( ( setting ) => {

				if ( setting.column_name !== column ) {
					return;
				}

				let cell = AdminColumns.Table.Cells.get( setting.id, column );
				let iEdit = cell.IEdit;

				if ( iEdit ) {
					cell.dataStorage.storeRevision( value );
					iEdit.Editable.setValue( setting.value );
					iEdit.setValueFromRowHTML( rows[ setting.id ] );
				}

			} );
		} );

	}

	getRows( ids ) {
		return jQuery.ajax( {
			url : window.location.href,
			method : 'post',
			data : {
				ac_action : 'get_table_rows',
				ac_ids : ids,
				_ajax_nonce : AC.ajax_nonce
			}
		} );
	}

}