import {Format} from 'admin-columns-js/helper/strings';

const i18n = {
	SELECTED : ACP_Editing.i18n.bulk_edit.selecting.selected,
	SELECTALL : ACP_Editing.i18n.bulk_edit.selecting.select_all
};

const SELECTORS = {
	ROW : 'acp-be-noticerow'
};

export default class BulkNotice {
	constructor( Table ) {
		this.notice = null;
		this.Table = Table;
	}

	addNotice() {
		this.createNotice();
		this.createNoticeRow();
		this.refreshNoticeRow();

		document.querySelectorAll( '.hide-column-tog' ).forEach( cb => {
			cb.addEventListener( 'click', () => {
				this.refreshNoticeRow()
			} );
		} )
	}

	removeNotice() {
		let notice = this.Table.el.querySelector( `.${SELECTORS.ROW}` );
		if ( notice ) {
			notice.remove();
		}
	}

	createNotice() {
		let notice = document.createElement( 'div' );

		notice.classList.add( 'acp-be-notice' );

		let p = document.createElement( 'p' );
		let text = Format( `${i18n.SELECTED} `, `<span data-acp-be-count></span>` );

		p.insertAdjacentHTML( 'beforeEnd', text );

		notice.append( p );

		this.notice = notice;
	}

	createSelectAll( number ) {
		if ( document.querySelector( '.ac-be-selectall' ) ) {
			return document.querySelector( '.ac-be-selectall' );
		}

		if ( number === false || number === 'all' ) {
			number = '';
		}

		if ( !this.notice ) {
			return;
		}

		let link = document.createElement( 'a' );
		link.innerHTML = Format( i18n.SELECTALL, number );
		link.classList.add( 'ac-be-selectall' );

		this.notice.querySelector( 'p' ).append( link );

		return link;
	}

	createNoticeRow() {
		let row = document.createElement( 'tr' );
		let col = document.createElement( 'td' );

		row.append( col );
		row.classList.add( SELECTORS.ROW );

		let colspan = AdminColumns.Table.el.querySelector( 'thead tr' ).children.length;

		col.setAttribute( 'colspan', colspan );
		col.append( this.notice );

		this.Table.el.querySelector( 'tbody' ).insertAdjacentElement( 'afterBegin', row );

		return row;
	}

	refreshNoticeRow() {
		let cells = this.Table.el.firstElementChild.firstElementChild.querySelectorAll( 'td,th' );
		let colspan = 0;

		cells.forEach( cell => {
			if ( !cell.classList.contains( 'hidden' ) ) {
				colspan++;
			}
		} );

		this.Table.el.querySelector( `.${SELECTORS.ROW} td` ).setAttribute( 'colspan', colspan );
	}

}