export default class FormTemplate {

	static input( name, value = null, attributes = {} ) {
		let input = document.createElement( 'input' );

		input.setAttribute( 'name', name );

		if ( value ) {
			input.value = value;
		}

		attributes = Object.assign( {}, {
			type : 'text'
		}, attributes );

		FormTemplate.setHtmlAttributes( input, attributes );

		return input;
	}

	static textarea( name, value = 'ss', attributes = {} ) {
		let textarea = document.createElement( 'textarea' );

		textarea.setAttribute( 'name', name );

		FormTemplate.setHtmlAttributes( textarea, attributes );

		return textarea;
	}

	static checkbox( name, value, label ) {
		let input = document.createElement( 'label' );
		input.classList.add('input__checkbox');
		input.innerHTML = `<input type="checkbox" name="${name}" class="input__checkbox__input" value="${value}"><span class="input__checkbox__label">${label}</span>`;

		return input;
	}

	static setHtmlAttributes( input, attributes ) {
		Object.keys( attributes ).forEach( ( attribute ) => {
			let value = attributes[ attribute ];

			input.setAttribute( attribute, value );

		} );
	}

	static inputGroup( label, input ) {
		return `
		<div class="input__group">
			<label>${label}</label>
			<div class="input__controlgroup">${input}</div>
		</div>
		`;
	}
}