import ColumnDataStorage from "../modules/data-storage";

export default class CellMiddleware {

	/**
	 * @param {Columns} model
	 * @param {Object} config
	 */
	static map( model, config ) {
		config.forEach( ( editvalue ) => {

			let cell = model.get( editvalue.id, editvalue.column_name );

			if ( !cell ) {
				return;
			}

			let settings = cell.getSettings().editable;

			if ( !settings ) {
				return;
			}

			if ( settings.hasOwnProperty( 'store_values' ) && !settings.store_values ) {
				editvalue.formatted_value = editvalue.value;
				editvalue.value = Object.keys( editvalue.value );
			}

			if ( settings.hasOwnProperty( 'store_single_value' ) && settings.store_single_value ) {
				editvalue.formatted_value = editvalue.value;
				editvalue.value = Object.keys( editvalue.value )[ 0 ];
			}

			cell.dataStorage = new ColumnDataStorage( editvalue )
		} );

		return model;
	}

}