import Abstract from "./abstract";

export default class Togglable extends Abstract {

	getEditable() {
		let editable = this.Editables.get( 'toggle' );

		return editable ? editable : false;
	}

	map() {
		this.args.options = this.column.editable.options;
		this.args.showbuttons = false;

		return this.args;
	}

	flattenOptionArray( options ) {
		let result = [];
		options.forEach( ( option ) => {
			result.push( option.label );
		} );

		return result;
	}

}