'use strict';

import Editables from './modules/editables';
import Helper from './modules/helper';
import BulkEdit from './modules/bulk-edit';
import InlineEdit from './modules/inline-edit';
import ColumnMiddleware from './middleware/columns';
import CellMiddleware from './middleware/cells';
import TableUpdate from './modules/table-update';

require( 'admin-columns-js/polyfill/customevent' );
require( 'admin-columns-js/polyfill/nodelist' );
require( './helpers/polyfill' );

global.AdminColumns = typeof AdminColumns !== "undefined" ? AdminColumns : {};

/*
 * DOM ready
 */

document.addEventListener( "DOMContentLoaded", () => {
	if ( !AdminColumns.hasOwnProperty( 'Table' ) ) {
		return;
	}

	ColumnMiddleware.map( AdminColumns.Table.Columns, ACP_Editing_Columns );

	AdminColumns.Editing = {};
	AdminColumns.Editing.Editables = new Editables();
	AdminColumns.Editing.Editables.initEditables();
	AdminColumns.Editing.Helper = Helper;

	if ( hasBulkEditColumns( ACP_Editing_Columns ) ) {
		AdminColumns.Editing.BulkEdit = new BulkEdit( AdminColumns.Table, AdminColumns.Editing.Editables );
		require( './modules/bulk-modal' );
	}

	AdminColumns.Editing.TableUpdate = new TableUpdate();

	if ( hasInlineEditColumns( ACP_Editing_Columns ) ) {
		let Editing_items = jQuery.ajax( {
			url : ajaxurl,
			method : 'post',
			data : {
				action : 'acp_editing_single_request',
				method : 'get_editable_values',
				ids : AdminColumns.Table._ids,
				list_screen : AC.list_screen,
				layout : AC.layout,
				_ajax_nonce : AC.ajax_nonce
			}
		} );

		Editing_items.done( ( response ) => {
			if ( !response.success || !response.data ) {
				console.info( 'Admin Columns Inline Edit could not be loaded' );
				return;
			}

			if ( !response.data.hasOwnProperty( 'editable_values' ) ) {
				return;
			}

			let ie_enabled = (ACP_Editing.inline_edit.hasOwnProperty( 'active' ) && ACP_Editing.inline_edit.active);

			if ( ACP_Editing.inline_edit.hasOwnProperty( 'persistent' ) && ACP_Editing.inline_edit.persistent ) {
				ie_enabled = true;
			}

			CellMiddleware.map( AdminColumns.Table.Cells, response.data.editable_values );
			AdminColumns.Editing.InlineEdit = new InlineEdit( AdminColumns.Table, AdminColumns.Editing.Editables, ie_enabled );
		} );
	} else {
		let ieditToggle = document.querySelector( '.ac-table-button.-toggle.-iedit' );
		if ( ieditToggle ) {
			ieditToggle.remove();
		}
	}

} );

const hasInlineEditColumns = ( columns ) => {
	for ( const column_name of Object.keys( columns ) ) {
		let settings = columns[ column_name ];

		if ( settings.hasOwnProperty( 'inline_edit' ) && settings.inline_edit ) {
			return true;
		}
	}

	return false;
};

const hasBulkEditColumns = ( columns ) => {
	for ( const column_name of Object.keys( columns ) ) {
		let settings = columns[ column_name ];

		if ( settings.hasOwnProperty( 'bulk_edit' ) && settings.bulk_edit ) {
			return true;
		}
	}

	return false;
};