import Abstract from './abstract';

export class Date extends Abstract {

	getEditable() {
		let editable = this.Editables.get( 'date' );

		return editable ? editable : false;
	}

	map() {
		this.args.showbuttons = false;

		return this.args;
	}

}

export class DateTime extends Abstract {


	map() {
		this.args.timeformat = this.column.editable.timeformat;

		return this.args;
	}

	getEditable() {
		let editable = this.Editables.get( 'date_time' );

		return editable ? editable : false;
	}

}