import AbstractEditable from './abstract';

export default class WPLibraryEditable extends AbstractEditable {

	render() {
		this.library = wp.media( {
			multiple : this._args.multiple,
			library : this._args.library,
			title : this._args.title
		} );

		this.displaySaveButton( false );
		this.currentValues = [];
		this._addEvents();
	}

	valueToInput( value ) {
		if ( value ) {
			this.currentValues = Array.isArray( value ) ? value : [ value ];
		}
		if ( !this._args.showbuttons ) {
			this.library.open();
		}
	}

	getValue() {
		return this.currentValues;
	}

	getTemplate() {
		return `
		<a href="#" class="wplib button">Select</a>
		<div class="aceditable__selected"></div>`;
	}

	_addEvents() {
		this.getElement().querySelector( '.wplib' ).addEventListener( 'click', ( e ) => {
			e.preventDefault();
			this.library.open();
		} );

		this.library.on( 'open', () => {
			let selection = this.library.state().get( 'selection' );
			let value = this.currentValues;

			if ( !value || this._args.disableSelection ) {
				return;
			}

			value.forEach( ( id ) => {
				let attachment = wp.media.attachment( id );

				attachment.fetch();
				selection.add( attachment ? [ attachment ] : [] );
			} );
		} );

		this.library.on( 'select', () => {
			let selection = this.library.state().get( 'selection' ).toJSON();
			let ids = [];

			selection.forEach( ( selected ) => {
				ids.push( selected.id );
			} );

			this.currentValues = this._args.multiple ? ids : ids[ 0 ];

			if ( !this._args.showbuttons ) {
				this.save();
				this.close();
			}

			this.SetImagesFromSelection( this.library.state().get( 'selection' ).models );
			this.displayLibraryButton( false );
			this.displaySaveButton( true );
		} );

		this.library.on( 'close', () => {
			if ( this._args.showbuttons ) {
				return;
			}

			setTimeout( () => {
				this.close()
			}, 0 );
		} );

	}

	displaySaveButton( show ) {
		let button = this.getElement().querySelector( '.aceditable__form__controls' );
		if ( show ) {
			button.style.display = 'inline-block';
		} else {
			button.style.display = 'none';
		}

	}

	displayLibraryButton( show = true ) {
		let button = this.getElement().querySelector( '.wplib' );
		if ( show ) {
			button.style.display = 'inline-block';
		} else {
			button.style.display = 'none';
		}
	}

	SetImagesFromSelection( models ) {
		let container = this.getElement().querySelector( '.aceditable__selected' );

		if ( models.length === 0 ) {
			return;
		}

		jQuery.fn.qtip.zindex = 20000000;

		models.forEach( ( model ) => {
			let wrapper = document.createElement( 'div' );
			let element = null;

			switch ( model.attributes.type ) {
				case 'image' :
					element = WPLibraryEditable.getImageElement( model );
					break;
				default:
					element = WPLibraryEditable.getFileElement( model );
			}

			if ( !element ) {
				return;
			}

			wrapper.append( element );
			wrapper.append( WPLibraryEditable.getInfoElement( model ) );
			wrapper.classList.add( 'aceditable__selected__item' );

			jQuery( wrapper ).qtip( {
				content : {
					text : function() {
						return jQuery( this ).find( '.aceditable__selected__item__info' ).html();
					}
				},
				position : {
					my : 'top center',
					at : 'bottom center'
				},
				style : {
					tip : true,
					classes : 'qtip-tipsy'
				}
			} );

			element.addEventListener( 'click', ( e ) => {
				e.preventDefault();
				this.library.open();
			} );

			container.append( wrapper );
		} );
	}

	static getInfoElement( model ) {
		let element = document.createElement( 'div' );
		element.classList.add( 'aceditable__selected__item__info' );

		element.innerHTML = `
			<div>${model.attributes.title}</div>
			<div>${model.attributes.filename}</div>
			<div>ID: ${model.attributes.id}</div>
		`;

		return element;
	}

	static getImageElement( model ) {
		let element = document.createElement( 'img' );
		let url = model.attributes.url;

		if ( model.attributes.sizes.hasOwnProperty( 'thumbnail' ) ) {
			url = model.attributes.sizes.thumbnail.url;
		}

		element.setAttribute( 'src', url );
		element.classList.add( 'image' );

		return element;
	}

	static getFileElement( model ) {
		let element = document.createElement( 'img' );
		element.setAttribute( 'src', model.attributes.icon );
		element.classList.add( 'file' );

		return element;
	}

	_setDefaults() {
		this._defaults.type = 'media';
		this._defaults.title = 'Media';
		this._defaults.multiple = true;
		this._defaults.showbuttons = false;
		this._defaults.disableSelection = false;
		this._defaults.library = {
			uploadedTo : true
		};
	}
}