import AbstractEditable from './abstract';

export default class Select2Editable extends AbstractEditable {

	render() {
		if ( !this.hasOwnProperty( 'formattedValues' ) ) {
			this.formattedValues = [];
		}

		let select = this.getElement().querySelector( '.acs2' );

		if ( this._args.select2.multiple ) {
			select.setAttribute( 'multiple', true );
		}

		if ( !this._args.showbuttons ) {
			this.getElement().querySelector( '.aceditable__form__controls' ).style.display = 'none';
		}
	}

	valueToInput( value ) {

		value = value ? value : [];
		value = Array.isArray( value ) ? value : [ value ];

		let self = this;
		let select = self.getElement().querySelector( '.acs2' );
		let $select = jQuery( select );

		if ( !self._args.ajax && value ) {
			let selected = Array.isArray( value ) ? value : [ value ];

			this._args.select2.data.map( ( d ) => {
				d.selected = false;
			} );

			selected.forEach( ( value ) => {
				self.setSelectedOption( value );
			} );
		}

		if ( self._args.ajax && value ) {

			value.forEach( ( record ) => {

				Object.keys( record ).forEach( ( k ) => {
					let option_element = document.createElement( 'option' );

					option_element.setAttribute( 'selected', true );
					option_element.value = k;
					option_element.text = record[ k ];
					select.append( option_element );
				} );

			} )
		}

		$select.ac_select2(
			self._args.select2
		);

		if ( !this._args.showbuttons ) {

			$select.on( 'change', () => {
				self.save();
			} );

		}

	}

	setSelectedOption( value ) {
		let data = this._args.select2.data;

		data.some( ( option ) => {
			if ( option.value.toString() === value ) {
				option.selected = true;

				return true;
			}

			return false;
		} );

		return data;
	}

	getValue() {
		let select = this.getElement().querySelector( 'select.acs2' );

		if ( this._args.select2.multiple ) {
			return this.getSelectedValues();
		}

		return select.value;
	}

	getSelectedValues() {
		const selected = this.getElement().querySelectorAll( 'select.acs2 option:checked' );

		return Array.from( selected ).map( option => option.value );
	}

	getTemplate() {
		return `<select class="acs2"></select>`;
	}

	_setDefaults() {
		this._defaults.type = 'select2';
		this._defaults.showbuttons = true;
	}

}