import Middleware from '../middleware/editable';
import Popper from 'popper.js';
import Job from './bulk-processor';
import {Format} from 'admin-columns-js/helper/strings';
import BulkEditRow from "./bulk-edit-row";
import Notice from './bulk-notice';

const i18n = {
	UPDATE : Format( ACP_Editing.i18n.bulk_edit.form.update_values, '<span data-acp-be-count>0</span>' ),
	ARE_YOU_SURE : ACP_Editing.i18n.bulk_edit.form.are_you_sure,
	YES_UPDATE : ACP_Editing.i18n.bulk_edit.form.yes_update,
	CANCEL : ACP_Editing.i18n.cancel
};

const defaults = {
	total_count : ACP_Editing.bulk_edit.total_items
};

export default class BulkEdit {

	constructor( Table, Editables ) {
		this.Table = Table;
		this.Editables = Editables;
		this.Middleware = new Middleware( this.Editables );
		this.Notice = new Notice( this.Table );
		this.EditableModal = false;
		this.EditRow = new BulkEditRow( this );

		this._allSelected = false;
		this._isConfirmed = false;

		this.init();
	}

	init() {
		this.addTableEvents();
	}

	setConfirmed( confirm ) {
		this._isConfirmed = confirm;

		return this;
	}

	isConfirmed() {
		if ( ACP_Editing.bulk_edit.hasOwnProperty( 'show_confirmation' ) && !ACP_Editing.bulk_edit.show_confirmation ) {
			return true;
		}

		return this._isConfirmed;
	}

	onSave( editable, cell ) {
		if ( !this.isConfirmed() ) {
			this.confirm( editable );
			return;
		}

		let job = new Job( cell.column, editable.getValue(), this.getItemsToProcess(), this._allSelected );
		this._locked = true;
		this.EditableModal.setProcess( job.getElement() );
		this.Editable.disable();
		editable.getElement().querySelector( '.aceditable__form__controls [data-submit]' ).style.display = 'none';

		// Bind Events
		job.Events.addListener( 'close', () => {
			this.EditableModal.close();
		} );

		job.Events.addListener( 'finished', () => {
			AdminColumns.Editing.TableUpdate.updateSelectedCells( cell.column.name, editable.getValue() );
			this.EditableModal.setCanDismiss( true );
			this.EditableModal.toggleCloseButton( true );
		} );
	}

	onButtonTrigger( cell ) {

		let middleware = this.Middleware.get( cell.column );
		let editable = middleware.getEditable();
		let modal = AdminColumns.Editing.BulkEdit.Modal;

		this.setConfirmed( false );

		if ( !editable || this.EditableModal ) {
			return false;
		}

		let editableArgs = middleware.getArgs();
		editableArgs.showbuttons = true;
		editableArgs.mode = 'bulk';

		let Editable = new editable( editableArgs );
		let EditableModal = new modal( modal.appendMarkup(), Editable );

		Editable.getEditableTemplate().setSubmitButton( 'Update' );

		EditableModal.setTitle( `Bulk Edit ${cell.column.label}` );
		EditableModal.setTotal( this.getSelectedCount() );
		EditableModal.open();

		this.EditableModal = EditableModal;
		this.Editable = Editable;
		this.updateCount();

		// Bind Events
		Editable.Events.addListener( 'save', ( editable ) => {
			this.onSave( editable, cell );
		} );

		EditableModal.Events.addListener( 'close', () => {
			this.destroyEditable();
			this.EditableModal = null;
		} );

	}

	destroyEditable() {
		this.removeConfirm();
		this.EditableModal.destroy();
	}

	confirm( editable ) {
		this.removeConfirm();
		let confirm = document.createElement( 'div' );
		confirm.classList.add( 'acp-be-confirm' );
		confirm.innerHTML = `
			<span class="acp-be-confirm__caret"></span>
			<em class="acp-be-confirm__info">${i18n.UPDATE}</em>
			<div class="acp-be-confirm__controls"><strong>${i18n.ARE_YOU_SURE}</strong> <a data-confirm>${i18n.YES_UPDATE}</a> <a data-cancel>${i18n.CANCEL}</a></div>
		`;

		document.body.append( confirm );

		new Popper( editable.getEditableTemplate().el.querySelector( '[data-submit]' ), confirm, {
			modifiers : {
				preventOverflow : { enabled : false },
				hide : { enabled : false },
				arrow : { element : '.acp-be-confirm__caret' }
			}
		} );

		this._confirmElement = confirm;
		this.setConfirmed( false );
		this.updateCount();

		// Bind Events
		let elDialog = this.EditableModal.el.querySelector( '.ac-modal__dialog' );
		let elConfirm = confirm.querySelector( '[data-confirm]' );
		let elCancel = confirm.querySelector( '[data-cancel]' );

		elDialog.addEventListener( 'keypress', ( e ) => {
			if ( e.key === 'Enter' && document.querySelectorAll( '.acp-be-confirm' ).length > 0 ) {
				this.setConfirmed( true );
				this.removeConfirm();
				editable.save();
			}
		} );

		elConfirm.addEventListener( 'click', () => {
			this.setConfirmed( true );
			this.removeConfirm();
			editable.save();
		} );

		elCancel.addEventListener( 'click', () => {
			this.setConfirmed( false );
			this.removeConfirm();
		} );
	}

	addTableEvents() {
		let checkboxes = this.Table.el.querySelectorAll( '.check-column input[type=checkbox]' );

		checkboxes.forEach( ( checkbox ) => {
			checkbox.addEventListener( 'change', () => {
				this._allSelected = false;

				setTimeout( () => {
					if ( this.Table.Selection.getCount() > 1 ) {
						this.toggleEditRows( true );
					} else {
						this.toggleEditRows( false )
					}

					if ( this.Table.Selection.isAllSelected() && (defaults.total_count && this.Table.Selection.getCount() < defaults.total_count) ) {
						let link = this.Notice.createSelectAll( defaults.total_count );
						defaults.total_count = defaults.total_count ? defaults.total_count : 'all';

						if ( link ) {
							link.style.display = 'inline';
							link.addEventListener( 'click', () => {
								link.style.display = 'none';
								this._allSelected = true;
								this.updateCount();
							} );
						}
					}

					this.updateCount();
				} );

			} );
		} );

		this.Table.el.addEventListener( 'scroll', ( e ) => {
			let table = AdminColumns.Table.el;
			let notice = table.querySelector( '.acp-be-noticerow .acp-be-notice p' );

			if ( !notice ) {
				return;
			}

			if ( document.body.classList.contains( 'acp-overflow-table' ) ) {
				notice.style.left = `${table.scrollLeft}px`;
			} else {
				notice.style.left = 0;
			}
		} );
	}

	toggleEditRows( show = true ) {
		this.Notice.removeNotice();
		this.EditRow.remove();

		if ( show ) {
			this.Notice.addNotice();
			this.EditRow.add();
		}
	}

	getSelectedCount() {
		let count = this.Table.Selection.getCount();

		if ( this._allSelected ) {
			count = defaults.total_count;
		}

		return count;
	}

	updateCount() {
		document.querySelectorAll( '[data-acp-be-count]' ).forEach( ( el ) => {
			el.innerHTML = this.getSelectedCount();
		} );
	}

	getItemsToProcess() {
		if ( this._allSelected ) {
			return defaults.total_count;
		}

		return this.Table.Selection.getIDs();
	}

	removeConfirm() {
		if ( !this._confirmElement ) {
			return;
		}

		this._confirmElement.remove();
	}

}