import AbstractEditable from './abstract';

export default class ColorEditable extends AbstractEditable {

	valueToInput( value ) {
		let input = this.getElement().querySelector( 'input' );

		if ( value && input ) {
			input.value = value;
		}

		jQuery( input ).wpColorPicker();
	}

	getValue() {
		return this._element.querySelector( 'input' ).value;
	}

	getTemplate() {
		return this._template.form.input( 'color' ).outerHTML;
	}

	_setDefaults() {
		this._defaults.type = 'color';
	}

}