import AbstractEditable from "../editable/abstract";
import CheckboxEditable from "../editable/checkbox";
import ColorEditable from "../editable/color";
import DateEditable from "../editable/date";
import DateTimeEditable from "../editable/date_time";
import TextEditable from "../editable/text";
import TextareaEditable from "../editable/textarea";
import ToggleEditable from "../editable/toggle";
import SelectEditable from "../editable/select";
import Select2Editable from "../editable/select2_dropdown";
import WPLibraryEditable from "../editable/media";
import MultiInputEditable from "../editable/multi_input";
import TaxonomyEditable from "../editable/taxonomy";

export default class Editables {

	constructor() {
		this.editables = {};
	}

	initEditables() {
		this.editables._abstract = AbstractEditable;
		this.editables.text = TextEditable;
		this.editables.textarea = TextareaEditable;
		this.editables.number = TextEditable;
		this.editables.checklist = CheckboxEditable;
		this.editables.color = ColorEditable;
		this.editables.select = SelectEditable;
		this.editables.select2 = Select2Editable;
		this.editables.date = DateEditable;
		this.editables.date_time = DateTimeEditable;
		this.editables.toggle = ToggleEditable;
		this.editables.wp_library = WPLibraryEditable;
		this.editables.multi_input = MultiInputEditable;
		this.editables.taxonomy = TaxonomyEditable;

		document.dispatchEvent( new CustomEvent( 'AC_Editing_Register_Editables', { detail : this } ) );
	}

	registerEditable( name, object ) {
		this.editables[ name ] = object;

		return this;
	}

	get( type ) {
		if ( !this.editables.hasOwnProperty( type ) ) {
			return false;
		}

		return this.editables[ type ];
	}

	abstract() {
		return this.get( '_abstract' );
	}

}