import {Actions, MediaActions} from "./inline-edit-actions";
import Popper from 'popper.js';
import {insertAfter} from '../helpers/elements';
import {getColumnValueFromRowHTML} from "../helpers/rows";

const Selectors = {
	CELL : 'acp-ie-editable',
	VALUE : 'acp-ie-value'
};

export default class InlineEditCell {

	constructor( InlineEdit, Cell, Middleware, editable_class ) {
		this._editableClass = editable_class;

		this.InlineEdit = InlineEdit;
		this.Cell = Cell;
		this.Middleware = Middleware;
		this.Editable = null;
		this.Popper = null;

		this.wrapCellContent();
		this.attachEditable();
		this.insertControls();
		this.attachEvents();
	}

	attachEditable() {
		if ( !this.Cell.dataStorage || this.Editable ) {
			return;
		}

		let value = this.Cell.dataStorage.getFormattedValue();
		if ( value === null ) {
			return;
		}

		this.Editable = new this._editableClass( this.Middleware.getArgs() );
		this.Editable.setValue( value );

		this.Editable.Events.addListener( 'save', ( editable ) => {
			this.saveValue( editable.getValue() );
		} );

		this.Editable.Events.addListener( 'close', () => {
			PopperGlobalEvents.destroy();
			this.destroyPopper();
		} );
	}

	attachEvents() {
		this.getTargetElement().addEventListener( 'click', ( e ) => {
			if ( !this.InlineEdit._enabled ) {
				return;
			}

			e.preventDefault();
			e.stopPropagation();

			this.openEditable();
		} );

		document.addEventListener( 'ACP_InlineEditing_Close', () => {
			if ( this.Popper ) {
				this.Editable.close();
			}
		} );
	}

	saveValue( value ) {
		this.Editable.toggleLoading( true );

		this.Cell.dataStorage.storeRevision( value ).save().done( ( response ) => {
			if ( !response.success ) {
				this.Editable.toggleLoading( false );
				this.Editable.setError( response.data.message );

				return;
			}

			this.setValue( response.data );
			this.Editable.setValue( response.data.value ).close();
			this.Editable.toggleLoading( false );

			let data = {
				column : this.Cell.getName(),
				item : this.Cell.getObjectID(),
				value : response.data.value
			};
			document.dispatchEvent( new CustomEvent( 'ACP_InlineEditing_After_Save', { detail : data } ) );
		} );
	}

	openEditable() {
		let InlineEditable = AdminColumns.Editing.InlineEdit;
		InlineEditable.closeOpenEditables();
		let container = this.getPopperContainer();

		container.append( this.Editable.get() );

		this.popper = new Popper( this.getTargetElement(), container, {
			modifiers : {
				arrow : { element : '.ac-edit-popper__caret' }
			}
		} );

		InlineEditable._open_editables.push( this.Editable );
		PopperGlobalEvents.bind();
	}

	/**
	 * Wraps the content of a cell for better control
	 */
	wrapCellContent() {
		let settings = this.Cell.getSettings().editable;
		let target = document.createElement( 'span' );

		this.Cell.el.classList.add( Selectors.CELL );
		this.Cell.el.classList.add( `${Selectors.CELL}--${settings.type}` );

		target.classList.add( Selectors.VALUE );
		while ( this.Cell.el.firstChild ) {
			target.appendChild( this.Cell.el.firstChild );
		}

		this.Cell.el.append( target );

		let InvalidElements = target.querySelectorAll( '.row-actions' );
		if ( InvalidElements ) {
			InvalidElements.forEach( ( element ) => {
				this.Cell.el.appendChild( element );
			} )
		}

		return target;
	}

	undo() {
		let undo = this.Cell.dataStorage.undo();

		if ( undo ) {
			this.Cell.dataStorage.save().done( ( response ) => {
				this.setValue( response.data );
				this.Editable.setValue( response.data.value );
			} );
		}
	}

	redo() {
		let redo = this.Cell.dataStorage.redo();

		if ( redo ) {
			this.Cell.dataStorage.save().done( ( response ) => {
				this.setValue( response.data );
				this.Editable.setValue( response.data.value );
			} );
		}
	}

	clear() {
		this.Cell.dataStorage.storeRevision( '' ).save().done( ( response ) => {
			this.setValue( response.data );
			this.Editable.setValue( response.data.value );
		} );
	}

	setCellValue( value ) {
		let rowActions = this.Cell.el.querySelector( '.row-actions' );
		this.Cell.el.innerHTML = value;

		if ( rowActions ) {
			this.Cell.el.append( rowActions );
		}

		this.afterSetValue();
	}

	setValue( data ) {
		let displayValue = data.display_value;

		if ( displayValue ) {
			this.setCellValue( displayValue );
		} else {
			this.getTargetElement().style.backgroundColor = '#f2f093';
			this.getRowsHTML( [ data.id ] ).done( ( response ) => {
				if ( !response.hasOwnProperty( 'data' ) ) {
					return;
				}

				this.setValueFromRowHTML( response.data.table_rows[ data.id ] );
			} );
		}
	}

	/**
	 * Give the value cell a visual feedback
	 */
	valueUpdateVisual() {
		const el = this.getTargetElement();
		if ( !el ) {
			return;
		}

		el.style.animation = 'acp-value-highlight .9s';
	}

	/**
	 * Return the element that contains the value
	 *
	 * @returns {any}
	 */
	getTargetElement() {
		const el = this.Cell.el.querySelector( `.${Selectors.VALUE}` );

		return el ? el : false;
	}

	insertControls() {
		let actions = new Actions( this );
		actions.render();

		if ( this.Middleware.hasMediaActions() ) {
			let mediaActions = new MediaActions( this );
			mediaActions.render();
		}

		this.moveActions();
	}

	moveActions() {
		const settings = this.Cell.getSettings().editable;

		if ( !settings.hasOwnProperty( 'js' ) || !settings.js.hasOwnProperty( 'selector' ) ) {
			return;
		}

		let el = this.Cell.el.querySelector( settings.js.selector );
		let actions = this.Cell.el.querySelector( '.acp-ie-controls' );

		insertAfter( actions, el );
	}

	getRowsHTML( ids = [] ) {
		return jQuery.ajax( {
			url : window.location.href,
			method : 'post',
			data : {
				ac_action : 'get_table_rows',
				ac_ids : ids,
				_ajax_nonce : AC.ajax_nonce
			}
		} );
	}

	afterSetValue() {
		this.wrapCellContent();
		this.valueUpdateVisual();
		this.insertControls();
		this.attachEvents();

		this.Cell.el.dispatchEvent( new CustomEvent( 'ACP_InlineEditing_After_SetValue' ) );
	}

	setValueFromRowHTML( rowHTML ) {
		let column = this.Cell.getSettings();

		this.setCellValue( getColumnValueFromRowHTML( rowHTML, column.name ) );
	}

	destroyPopper() {
		document.querySelectorAll( '.ac-edit-popper' ).forEach( ( popper ) => {
			popper.remove();
		} );

		if ( this.popper ) {
			this.popper.destroy();
		}
	}

	getPopperContainer() {
		this.Popper = document.createElement( 'div' );
		this.Popper.classList.add( 'ac-edit-popper' );

		this.Popper.insertAdjacentHTML( 'afterbegin', '<span class="ac-edit-popper__caret">' );

		document.body.append( this.Popper );

		return this.Popper;
	}

}

class PopperGlobalEvents {
	static bind() {
		document.addEventListener( 'keyup', PopperGlobalEvents.escapeEvent );
		document.addEventListener( 'click', PopperGlobalEvents.clickEvent );

		document.querySelectorAll( '.select2-container--acs2' ).forEach( c => {
			c.addEventListener( 'click', e => {
				e.stopPropagation()
			} )
		} );

	}

	static destroy() {
		document.removeEventListener( 'keyup', PopperGlobalEvents.escapeEvent );
		document.removeEventListener( 'click', PopperGlobalEvents.clickEvent );
	}

	static escapeEvent( e ) {
		if ( e.key === "Escape" ) {
			document.dispatchEvent( new CustomEvent( 'ACP_InlineEditing_Close' ) );
		}
	}

	static clickEvent( e ) {
		if ( e.target === document.body ) {
			return;
		}

		if ( PopperGlobalEvents.checkInvalidParentClasses( e.target ) ) {
			return;
		}

		document.dispatchEvent( new CustomEvent( 'ACP_InlineEditing_Close' ) );
	}

	static checkInvalidParentClasses( el ) {
		let isInvalid = false;
		let invalidClasses = [
			'select2-container',
			'ui-datepicker',
			'ui-datepicker-header',
			'ui-datepicker-buttonpane'
		];

		invalidClasses.map( c => {
			if ( el.classList.contains( c ) )
				isInvalid = true;
		} );

		if ( isInvalid ) {
			return true;
		}

		if ( !el.parentElement ) {
			return false;
		}

		return PopperGlobalEvents.checkInvalidParentClasses( el.parentElement );
	}

}