import Abstract from "./abstract";

export class Media extends Abstract {

	getEditable() {
		let editable = this.Editables.get( 'wp_library' );

		return editable ? editable : false;
	}

	map() {
		let editable = this.column.editable;

		this.args.showbuttons = false;
		this.args.multiple = !!(editable.hasOwnProperty( 'multiple' ) && editable.multiple);

		if ( editable.hasOwnProperty( 'attachment' ) && editable.attachment.hasOwnProperty( 'library' ) ) {
			this.args.library = editable.attachment.library;
		}

		if ( editable.hasOwnProperty( 'attachment' ) &&
			editable.attachment.hasOwnProperty( 'disable_select_current' ) &&
			editable.attachment.disable_select_current ) {
			this.args.disableSelection = true;
		}

		return this.args;
	}

	hasMediaActions() {
		return true;
	}

}

export class Attachment extends Media {

	map() {
		super.map();

		this.args.attachment = true;

		return this.args;
	}

}