import {Format} from 'admin-columns-js/helper/strings';
import EventEmitter from './event-emitter';

const i18n = {
	FINISHED : ACP_Editing.i18n.bulk_edit.feedback.finished,
	UPDATING : ACP_Editing.i18n.bulk_edit.feedback.updating,
	PROCESSED : Format( ACP_Editing.i18n.bulk_edit.feedback.processed, '<span data-processed>0</span>', '<span data-total-items></span>' ),
	FAIL : ACP_Editing.i18n.bulk_edit.feedback.failure,
	DONE : ACP_Editing.i18n.done,
	DONE_DESELECT : ACP_Editing.i18n.bulk_edit.selecting.done_deselect,
	CANCEL : ACP_Editing.i18n.cancel,
	ERROR : Format( ACP_Editing.i18n.bulk_edit.feedback.error, '<span data-errors>0</span>' ),
};

const ProcessTemplate = `
		<div class="acp-be-progress">
			<div class="acp-be-progress__timer" data-timer></div>
			<div class="acp-be-progress__process">${i18n.PROCESSED} (<span data-percentage></span>)</div>
		</div>

		<div class="acp-be-statusbar">
			<div class="acp-be-statusbar__progress" data-status-percentage></div>
		</div>

		<div class="errors" style="display: none;">
			<div class="errors__info">${i18n.ERROR}</div>
			<div class="errors__lines"></div>
		</div>

		<button data-close class="button">Cancel</button>
`;

export default class Processor {
	constructor( column, value, items, processAll = false ) {
		this.Events = new EventEmitter();
		this.column = column;
		this.value = value;
		this._items = items;
		this._total = Array.isArray( items ) ? items.length : items;
		this._processed = 0;
		this._perPage = ACP_Editing.bulk_edit.updated_rows_per_iteration;
		this._processAll = processAll;

		this._isCanceled = false;
		this._isFinished = false;

		this.errors = [];
		this.timer = {
			seconds : 0,
			minutes : 0,
		};

		this.prepareElement();
		this.startTimer();
		this.prepareJob();
		this.attachCancelEvent();
		this.updateTotal();
		this.updatePercentage( 0 );
	}

	getElement() {
		return this.element;
	}

	prepareJob( page = 1 ) {
		if ( !this._processAll ) {
			this.runJob();
			return;
		}

		if ( page === 1 ) {
			this._items = [];
		}

		jQuery.ajax( {
			url : window.location.href,
			method : 'get',
			data : {
				ac_action : 'get_editable_rows',
				ac_page : page,
				_ajax_nonce : AC.ajax_nonce
			}
		} ).done( ( response ) => {
			if ( !response.hasOwnProperty( 'success' ) || !response.success ) {
				return;
			}

			if ( !response.data.hasOwnProperty( 'editable_rows' ) ) {
				return;
			}

			this._items = [].concat( this._items, response.data.editable_rows );
			this._total = this._items.length;

			if ( response.data.rows_per_iteration === response.data.editable_rows.length ) {
				this.updateTotal();
				this.prepareJob( page + 1 );
				return;
			}

			this.updateTotal();
			this.runJob();
		} );

	}

	runJob() {
		if ( this._isCanceled ) {
			return;
		}

		let self = this;
		let items = self._items;
		let item_set = items.splice( 0, self._perPage );

		if ( item_set.length === 0 ) {
			self.onComplete();
			return;
		}

		this.startUpdateThread( item_set ).then( ( response ) => {
			if ( response.success ) {
				let num = response.data.total;
				self._processed += num;
				this.updateProcessed( self._processed );

				if ( num > 0 && !self._isCanceled ) {
					self.runJob();
				} else {
					self.onComplete();
				}

			} else {
				this.onFailure();
			}
		} );
	}

	startUpdateThread( ids ) {
		return jQuery.ajax( {
			url : ajaxurl,
			data : {
				action : 'acp_editing_bulk_request',
				method : 'save',
				list_screen : AC.list_screen,
				layout : AC.layout,
				column : this.column.name,
				ids : ids,
				value : this.value,
				_ajax_nonce : AC.ajax_nonce
			},
			method : 'POST',
			dataType : 'json'
		} );
	}

	prepareElement() {
		let element = document.createElement( 'div' );
		element.classList.add( 'acp-be-processing' );
		element.innerHTML = ProcessTemplate;

		this.element = element;
	}

	attachCancelEvent() {
		let button = this.element.querySelector( '[data-close]' );
		if ( !button ) {
			return;
		}

		button.addEventListener( 'click', () => {
			if ( this._isFinished ) {
				button.remove();
				this.Events.emit( 'close', {} );

			} else {
				this._isCanceled = true;
				this.onComplete();
			}
		} );
	}

	processResponse( response ) {
		this.updateProcessed( response.num_rows_processed );
	}

	updateTotal() {
		this.element.querySelector( '[data-total-items]' ).innerHTML = this._total;
	}

	updateProcessed( number ) {
		this.element.querySelector( '[data-processed]' ).innerHTML = number;

		let percentage = Math.round( (number / this._total) * 100 );
		this.updatePercentage( percentage );
	}

	updatePercentage( percentage ) {
		this.element.querySelector( '[data-percentage]' ).innerHTML = `${percentage}%`;
		this.element.querySelector( '[data-status-percentage]' ).style.width = `${percentage}%`;
	}

	onComplete() {
		if ( !this._isCanceled ) {
			this.element.querySelector( '.acp-be-progress__process' ).innerHTML = Format( i18n.FINISHED, this._total ) + ' (100%) <span class="dashicons dashicons-yes"></span>';
		}

		this.element.querySelector( '[data-close]' ).innerHTML = i18n.DONE;
		this.element.querySelector( '[data-close]' ).classList.add( 'button-primary' );
		this.element.querySelector( '[data-status-percentage]' ).classList.add( '-finished' );
		this._isFinished = true;
		this.stopTimer();
		this.canDismiss = false;
		this.addDeselectDoneButton();

		this.Events.emit( 'finished' );
	}

	addDeselectDoneButton() {
		let button = new DoneButton();

		button.el.addEventListener( 'click', ( e ) => {
			e.preventDefault();

			AdminColumns.Table.el.querySelectorAll( '.check-column input[type=checkbox]:checked' ).forEach( cb => {
				cb.checked = false;
				cb.dispatchEvent( new Event( 'change' ) );
			} );

			this.Events.emit( 'close' );
		} );

		this.element.append( button.el );
	}

	startTimer() {
		this.addSecond();
		this.timerInterval = setInterval( () => { this.addSecond() }, 1000 );
	}

	stopTimer() {
		clearInterval( this.timerInterval );
	}

	addSecond() {
		let element = this.element.querySelector( '[data-timer]' );
		this.timer.seconds++;
		if ( this.timer.seconds >= 60 ) {
			this.timer.seconds = 0;
			this.timer.minutes++;
		}

		element.textContent = (this.timer.minutes ? (this.timer.minutes > 9 ? this.timer.minutes : "0" + this.timer.minutes) : "00") + ":" + (this.timer.seconds > 9 ? this.timer.seconds : "0" + this.timer.seconds);
	}

}

class DoneButton {
	constructor() {
		this.createElement();
	}

	createElement() {
		let button = document.createElement( 'button' );
		button.classList.add( 'button', 'done-deselect' );
		button.innerText = i18n.DONE_DESELECT;
		button.dataset.close = true;

		this.el = button;
	}
}