import AbstractEditable from './abstract';

export default class SelectEditable extends AbstractEditable {

	render() {
		let select = this.getElement().querySelector( 'select' );

		Object.keys( this._args.options ).forEach( ( k ) => {
			let option = this._args.options[ k ];
			let el = document.createElement( 'option' );

			el.setAttribute( 'value', option.value );
			el.innerText = option.text;

			select.append( el );
		} );
	}

	valueToInput( value ) {
		if ( !value ) {
			return;
		}

		this.getElement().querySelector( 'select' ).value = value;
	}

	getValue() {
		return this.getElement().querySelector( 'select' ).value;
	}

	getTemplate() {
		return `<select name="select"></select>`;
	}

	_setDefaults() {
		this._defaults.type = 'select';
	}

}