import {insertAfter} from '../helpers/elements';

export class Actions {

	constructor( EditCell ) {
		this.EditCell = EditCell;
		this.Cell = this.EditCell.Cell;

		this.reset();
	}

	/*
	Remove the actions from the cell
	 */
	reset() {
		let container = this.Cell.el.querySelector( '.acp-ie-controls__items' );

		if ( container ) {
			container.remove();
		}
	}

	isRevisioningEnabled() {
		return !this.Cell.getSettings().editable.hasOwnProperty( 'disable_revisioning' );
	}

	render() {
		let container = Actions.createContainer();
		let valueCell = this.Cell.el.querySelector('.acp-ie-value');

		this.Cell.el.appendChild( container );

		if( valueCell ){
			insertAfter( container, valueCell );
		}

		Actions.appendButton( this.renderEdit(), container );
		if ( this.isRevisioningEnabled() ) {
			Actions.appendButton( this.renderUndo(), container );
			Actions.appendButton( this.renderRedo(), container );
		}
		Actions.appendButton( this.renderClear(), container );
	}

	static createContainer() {
		let container = document.createElement( 'div' );

		container.classList.add( 'acp-ie-controls' );

		return container;
	}

	static appendButton( button, element ) {
		if ( button ) {
			element.appendChild( button );
		}
	}

	renderEdit() {
		let el = Actions.createButtonElement( ACP_Editing.i18n.edit, '-edit' );

		el.insertAdjacentHTML( 'beforeEnd', `<span class="dashicons dashicons-edit"></span>` );

		el.addEventListener( 'click', ( e ) => {
			e.preventDefault();
			e.stopPropagation();

			this.EditCell.openEditable();
		} );

		return el;
	}

	renderUndo() {
		let storage = this.Cell.dataStorage;

		if ( storage.current_revision === 0 ) {
			return false;
		}

		let el = Actions.createButtonElement( ACP_Editing.i18n.undo, '-undo' );
		el.insertAdjacentHTML( 'beforeEnd', `<span class="dashicons dashicons-undo"></span>` );

		el.addEventListener( 'click', ( e ) => {
			e.preventDefault();
			e.stopPropagation();

			this.EditCell.undo();
		} );

		return el;
	}

	renderRedo() {
		let storage = this.Cell.dataStorage;

		if ( storage.current_revision >= (storage.revisions.length - 1) ) {
			return;
		}

		let el = Actions.createButtonElement( ACP_Editing.i18n.redo, '-redo' );
		el.insertAdjacentHTML( 'beforeEnd', `<span class="dashicons dashicons-redo"></span>` );

		el.addEventListener( 'click', ( e ) => {
			e.preventDefault();
			e.stopPropagation();

			this.EditCell.redo();
		} );

		return el;
	}

	renderClear() {
		let storage = this.Cell.dataStorage;

		if ( !this.Cell.getSettings().editable.clear_button ) {
			return;
		}

		if ( !storage.getValue() ) {
			return;
		}

		let el = Actions.createButtonElement( ACP_Editing.i18n.delete, '-clear' );
		el.insertAdjacentHTML( 'beforeEnd', `<span class="dashicons dashicons-no-alt"></span>` );

		el.addEventListener( 'click', ( e ) => {
			e.preventDefault();
			e.stopPropagation();

			this.EditCell.clear();
		} );

		return el;
	}

	static createButtonElement( title, className ) {
		let button = document.createElement( 'span' );

		button.classList.add( 'acp-ie-controls__item' );
		button.classList.add( className );
		button.setAttribute( 'title', title );

		return button;
	}

}

export class MediaActions extends Actions {

	render() {
		this.wrapImages();
	}

	wrapImages() {
		let images = this.Cell.el.querySelectorAll( '.ac-image' );
		let globalcontrols = this.Cell.el.querySelector( '.acp-ie-controls' );

		if ( !images ) {
			return;
		}

		images.forEach( ( image ) => {
			let id = image.dataset.mediaId;
			let wrapper = document.createElement( 'div' );
			let controls = document.createElement( 'div' );

			controls.classList.add( 'acp-ie-image-controls' );

			wrapper.dataset.mediaId = id;
			wrapper.classList.add( 'acp-ie-image-item' );

			image.parentNode.insertBefore( wrapper, image );

			wrapper.appendChild( image );
			wrapper.appendChild( controls );
		} );

		if ( this.Cell.getSettings().editable.multiple ) {
			this.addSingleImageDelete();
			globalcontrols.classList.add( '-multiple' );
		} else {
			this.moveActions();
			globalcontrols.classList.add( '-single' );
		}

	}

	addSingleImageDelete() {
		let images = this.Cell.el.querySelectorAll( '.acp-ie-image-item' );

		if ( !images ) {
			return;
		}

		images.forEach( ( image ) => {
			let controls = image.querySelector( '.acp-ie-image-controls' );
			let btn_remove = this.renderDelete();

			controls.append( btn_remove );
		} );
	}

	moveActions() {
		let controls = this.Cell.el.querySelector( '.acp-ie-controls' );
		let imageControls = this.Cell.el.querySelector( '.acp-ie-image-controls' );

		if ( !controls || !imageControls ) {
			return;
		}

		controls.querySelectorAll( '.acp-ie-controls__item' ).forEach( ( control ) => {
			imageControls.append( control );
		} );

		imageControls.classList.add( '-single' );
	}

	renderDelete() {
		let self = this;
		let btn_remove = MediaActions.createButtonElement( ACP_Editing.i18n.delete, '-delete' );
		btn_remove.insertAdjacentHTML( 'beforeend', `<span class="dashicons dashicons-no-alt"></span>` );

		btn_remove.addEventListener( 'click', ( e ) => {
			e.preventDefault();
			e.stopPropagation();

			let item = e.target.closest( '.acp-ie-image-item' );
			let dataStorage = self.Cell.dataStorage;
			let value = dataStorage.getValue();

			if ( Array.isArray( value ) ) {
				value = value.filter( ( e ) => parseInt( e ) !== parseInt( item.dataset.mediaId ) );
			} else {
				value = '';
			}

			this.EditCell.saveValue( value );
		} );

		return btn_remove;
	}

	renderDownload() {
		let el = Actions.createButtonElement( ACP_Editing.i18n.download, '-download' );
		el.insertAdjacentHTML( 'beforeEnd', `<span class="dashicons dashicons-migrate"></span>` );

		el.addEventListener( 'click', ( e ) => {
			e.preventDefault();
			e.stopPropagation();

			this.EditCell.redo();
		} );

		return el;
	}

}