const SELECTORS = {
	ROWCLASS : 'acp-be-editrow'
};

export default class BulkEditRow {

	constructor( BulkEdit ) {
		this.BulkEdit = BulkEdit;
		this.Table = BulkEdit.Table;
		this.Middleware = BulkEdit.Middleware;
	}

	add() {
		let thead = this.Table.el.getElementsByTagName( 'thead' )[ 0 ];
		let headers = thead.children[ 0 ].children;
		let row = document.createElement( 'tr' );

		row.classList.add( SELECTORS.ROWCLASS );

		for ( let i = 0; i < headers.length; i++ ) {
			let header = headers[ i ];
			let column = this.Table.Columns.get( header.id );
			let cell = document.createElement( 'td' );

			cell.classList.add( `column-${header.id}` );

			if ( header.id !== 'cb' && column.editable ) {

				if ( column.editable.hasOwnProperty( 'bulk_editable' ) && column.editable.bulk_editable === false ) {
					row.appendChild( cell );
					continue;
				}

				cell.innerHTML = '<button class="button">Bulk Edit</button>';
				cell.column = column;

				this.initEvents( cell );
			}

			row.appendChild( cell );
		}

		this.Table.el.querySelector( 'tbody' ).insertAdjacentElement( 'afterBegin', row );
		this.refresh();

		return row;
	}

	remove() {
		let row = this.Table.el.querySelector( `.${SELECTORS.ROWCLASS}` );
		if ( row ) {
			row.remove();
		}
	}

	initEvents( cell ) {
		cell.querySelector( 'button' ).addEventListener( 'click', ( e ) => {
			e.preventDefault();
			e.stopPropagation();

			this.BulkEdit.onButtonTrigger( cell );
		}, true );
	}

	getRow() {
		return this.Table.el.querySelector( `.${SELECTORS.ROWCLASS}` );
	}

	refresh() {
		let thead = this.Table.el.getElementsByTagName( 'thead' )[ 0 ];
		let headers = thead.children[ 0 ].children;

		for ( let i = 0; i < headers.length; i++ ) {
			let header = headers[ i ];
			let isHidden = header.classList.contains( 'hidden' );
			let col = this.getRow().querySelector( `.column-${header.id}` );

			if ( !col ) {
				continue;
			}

			if ( isHidden ) {
				col.classList.add( 'hidden' );
			} else {
				col.classList.remove( 'hidden' );
			}
		}
	}

}