import EditableTemplate from '../templates/editable';
import EventEmitter from '../modules/event-emitter';

const Defaults = {
	title : false,
	value : null,
	showbuttons : true,
	type : 'default',
	mode : 'inline'
};

export default class AbstractEditable {

	constructor( args ) {
		this._template = new EditableTemplate();
		this._element = null;
		this._args = this._getArgs( args );
		this._isInit = false;
		this._isDisabled = false;
		this.Events = new EventEmitter();

		new GlobalEvents();
	}

	/**
	 * @returns {AbstractEditable}
	 */
	close() {
		this.removeElement();
		Window.ACEditable = null;
		this.Events.emit( 'close', this );

		return this;
	}

	get() {
		this._element = this.createElement();
		this.beforeRender();
		this.render();
		this.valueToInput( this._args.value );
		this.initEvents();

		return this._element;
	}

	getEditableTemplate() {
		return this._template;
	}

	disable() {
		this._element.querySelectorAll( 'input, select, textarea, button' ).forEach( ( input ) => {
			input.disabled = true;
		} );

		this._isDisabled = true;

		return this;
	}

	enable() {
		this._element.querySelectorAll( 'input, select, textarea, button' ).forEach( ( input ) => {
			input.disabled = false;
		} );

		this._isDisabled = false;

		return this;
	}

	beforeRender() {
		this._template.setTemplate( this.getTemplate() );
		this._template.addClass( '-' + this._args.type );
		this._template.setError( '' );

		if ( !this._args.hasOwnProperty( 'showbuttons' ) && !this._args.showbuttons ) {
			this._template.showButtons( false );
		}
	}

	save() {
		if ( this._isDisabled ) {
			return;
		}

		let value = this.getValue();

		if ( !this.validate( value ) ) {
			return;
		}

		this.Events.emit( 'save', this );
	}

	initEvents() {
		if ( this._isInit ) {
			return;
		}

		this.getElement().addEventListener( 'click', ( e ) => {
			e.stopPropagation();
		} );

		this.getElement().querySelector( 'button[data-submit]' ).addEventListener( 'click', ( e ) => {
			e.preventDefault();

			this.save();
		} );

		this.getElement().querySelector( 'button[data-cancel]' ).addEventListener( 'click', ( e ) => {
			e.preventDefault();

			this.close();
		} );

		this.getElement().querySelector( 'form' ).addEventListener( 'submit', ( e ) => {
			e.preventDefault();

			this.save();
		} );

		this._isInit = true;
	}

	removeElement() {
		if ( this._element ) {
			this._element.remove();
		}
	}

	toggleLoading( loading = true ) {
		if ( !this.getElement() ) {
			return;
		}

		if ( loading ) {
			this.getElement().classList.add( '-loading' );
		} else {
			this.getElement().classList.remove( '-loading' );
		}
	}

	createElement() {
		return this._template.el;
	}

	setValue( value ) {
		this._args.value = value;

		return this;
	}

	setError( error ) {
		this._template.setError( error );
	}

	getElement() {
		return this._element;
	}

	getValue() {}

	getTemplate() {}

	render() {}

	valueToInput( value ) { }

	validate() {
		if ( AbstractEditable.hasFormValidation() ) {
			let form = this.getElement().querySelector( 'form' );
			if ( !this.getElement().querySelector( 'form' ).checkValidity() ) {
				form.reportValidity();
				return false;
			}
		}

		return true;
	}

	// Private

	_getArgs( args ) {
		this._defaults = { ...Defaults };
		this._setDefaults();

		return Object.assign( {}, this._defaults, args );
	}

	_setDefaults() {}

	_handleOutsideClickEvent() {
		this.close();
	}

	// Static

	static hasFormValidation() {
		return (typeof document.createElement( 'input' ).checkValidity === 'function');
	};

}

class GlobalEvents {

	constructor() {
		if ( AdminColumns.Editing.Editables.loaded ) {
			return;
		}

		this._setEscapeEvent();

		AdminColumns.Editing.Editables.loaded = true;
	}

	_setEscapeEvent() {
		document.addEventListener( 'keydown', function( e ) {
			if ( e.key === 'Escape' || e.key === 'Esc' ) {
				if ( Window.ACEditable ) {
					Window.ACEditable.close();
				}
			}
		}, true );
	}

	_setClickEvent() {
		document.addEventListener( 'click', function( e ) {
			if ( e.target === document.body ) {
				return;
			}

			if ( e.target.classList.contains( 'select2-results__option' ) ) {
				return;
			}

			if ( Window.ACEditable ) {
				Window.ACEditable.close();
			}
		} );
	}

}