import AbstractEditable from './abstract';

export default class DateTimeEditable extends AbstractEditable {

	render() {
		const input_date = this.getElement().querySelector( '[name=date]' );
		const input_hours = this.getElement().querySelector( '[name=time_hours]' );
		const input_minutes = this.getElement().querySelector( '[name=time_minutes]' );
		const input_seconds = this.getElement().querySelector( '[name=time_seconds]' );

		document.body.classList.add( 'ac-jqui' );

		DateTimeEditable.createSelectHours( input_hours, this._args.timeformat );
		DateTimeEditable.createSelectIncementalOptions( input_minutes );
		DateTimeEditable.createSelectIncementalOptions( input_seconds );

		jQuery( input_date ).datepicker( {
			dateFormat : 'yy-mm-dd',
			changeYear : true,
			showButtonPanel: true,
			firstDay : this._args.weekstart,
		} );

	}

	close() {
		document.body.classList.remove( 'ac-jqui' );

		super.close();
	}

	valueToInput( value ) {
		if ( !value ) {
			return;
		}

		let DateTime = new Date( value );

		if ( isNaN( DateTime.getTime() ) ) {
			return;
		}

		this.getElement().querySelector( '[name=date]' ).value = DateTime.getFullYear() + '-' + ('0' + (DateTime.getMonth() + 1)).slice( -2 ) + '-' + ('0' + DateTime.getDate()).slice( -2 );
		this.getElement().querySelector( '[name=time_hours]' ).value = ('0' + (DateTime.getHours())).slice( -2 );
		this.getElement().querySelector( '[name=time_minutes]' ).value = ('0' + (DateTime.getMinutes())).slice( -2 );
		this.getElement().querySelector( '[name=time_seconds]' ).value = ('0' + (DateTime.getSeconds())).slice( -2 );
	}

	getValue() {
		let date = this.getElement().querySelector( '[name=date]' ).value;
		let hours = this.getElement().querySelector( '[name=time_hours]' ).value;
		let minutes = this.getElement().querySelector( '[name=time_minutes]' ).value;
		let seconds = this.getElement().querySelector( '[name=time_seconds]' ).value;

		if ( !date || !hours || !minutes || !seconds ) {
			return false;
		}

		return `${date} ${hours}:${minutes}:${seconds}`;
	}

	getTemplate() {
		let input = this._template.form.input( 'date', '', { placeholder : 'yyyy-mm-dd', autocomplete : 'nope' } ).outerHTML;
		return `
			${input}
			<div class="input__time">
				<select name="time_hours"></select>				
				<select name="time_minutes"></select>				
				<select name="time_seconds"></select>				
			</div>
		`;
	}

	_setDefaults() {
		this._defaults.type = 'datetime';
		this._defaults.weekstart = 0;
		this._defaults.timeformat = 12;
	}

	static createSelectHours( select, timeformat ) {
		for ( let number = 0; number < 24; number++ ) {
			let option = document.createElement( 'option' );
			let _number = ('0' + number).slice( -2 );

			option.setAttribute( 'value', _number );
			option.text = (12 === timeformat) ? DateTimeEditable.formatAMPM( _number ) : _number;


			select.append( option );
		}
	}

	static formatAMPM( hours ) {
		let ampm = hours >= 12 ? 'pm' : 'am';

		hours = hours % 12;
		hours = hours ? hours : 12; // the hour '0' should be '12'

		return hours + ' ' + ampm;
	}

	static createSelectIncementalOptions( select, start = 0, end = 60 ) {
		for ( let number = start; number < end; number++ ) {
			let option = document.createElement( 'option' );
			let _number = ('0' + number).slice( -2 );
			option.setAttribute( 'value', _number );
			option.text = _number;

			select.append( option );
		}
	}

}